import { useEffect, useState } from "react";
import { useStore } from "src/MobxStore";
import { updateAssessment, getEquipments } from '../../services/inspectionClient';
import { CModal, CModalBody, CModalFooter, CModalHeader } from "@coreui/react";
import { DialogContentText } from "@material-ui/core";
import { NotificationManager } from "react-notifications";
const EditAssessment = ({ show, onClose, onEdit, inspectionId, assessment }) => {
    const store = useStore();
    const [customerType, setCustomerType] = useState(assessment.customerType);
    const [presentedDocument, setPresentedDocument] = useState(assessment.presentedDocument);
    const [reason, setReason] = useState(assessment.reason);
    const [customerLoad, setCustomerLoad] = useState(assessment.customerLoadDtos);
    const [loading, setLoading] = useState(false);
    const [newRow, setNewRow] = useState({ equipmentId: "", equipment:"", powerRate: "", quantity: "", totalKwh: "" });
    const [equipments, setEquipments] = useState([]);

    useEffect(() => {
        setLoading(true);
        getEquipments().then(result => {
            setEquipments(result.data);
            setLoading(false);
        }).catch(err => {
            setLoading(false)
            NotificationManager.error("Check your connection", "Network Error", 3000);
        });
    }, [])

    const handleUpdateAssessment = () => {
        setLoading(true);
        const customerRequest = customerLoad.map(item => ({
            id: item.id,
            equipmentId: item.equipmentId,
            powerRate: item.powerRate,
            quantity: item.quantity,
            totalKwh: item.totalKwh

        }));
        const request = {
            id: assessment.id,
            customerType: customerType,
            presentedDocument: presentedDocument,
            reason: reason,
            customerLoadUpdateRequestList: customerRequest
        }
        updateAssessment(store.login.token, inspectionId, request).then(response => {
            if (response.status) {
                NotificationManager.success(response.message, 'Success', 3000);
                setLoading(false);
                onEdit();
            } else {
                NotificationManager.error(response.message, 'Failed!', 3000);
                setLoading(false);
            }
        }).catch(err => {
            setLoading(false);
            NotificationManager.error("Check your connection", "Network Error", 3000);
        });
    }
    const handleChange = (e, index, key) => {
        const { value } = e.target;
        const newList = [...customerLoad];
        newList[index][key] = value;
        setCustomerLoad(newList);
    }
    const handleEquipmentChange = (e) => {
        const selectedEquipmentId = e.target.value;
        console.log("Selected Equipment Id  ",selectedEquipmentId);
        const selectedEquipment = equipments.find(equipment => equipment.id == selectedEquipmentId);
        console.log("Selected Equipment  ",selectedEquipment);
         setNewRow({
            ...newRow,
            equipmentId: selectedEquipment? selectedEquipment.id:"",
            equipment: selectedEquipment ? selectedEquipment.name : ""
        });
    };

    const handleAddRow = () => {
        setCustomerLoad([...customerLoad, newRow]);
        setNewRow({ equipmentId: "",equipment:"", powerRate: "", quantity: "", totalKwh: "" });
    }

    return (
        <CModal
            show={show}
            onClose={onClose}
            size='xl'
            backdrop
            close
            closeOnBackdrop={false}
        >
            <CModalHeader size="modal-lg" closeButton >
                <h5 style={{
                    textAlign: "center",
                    fontSize: 20,
                    fontWeight: "bold"
                }}>Edit Assesment
                </h5>
            </CModalHeader>
            <CModalBody>
                <DialogContentText id="alert-dialog-slide-description">

                    {<p style={{
                        textAlign: "center",
                        fontSize: 32,
                        fontWeight: "bold",
                        textDecoration: "underline"
                    }}>{"Inspection Assessment"}</p>}

                </DialogContentText>

                <div style={{ paddingTop: 5 }} />

                <div className="d-flex justify-content-between" style={{ paddingTop: 3 }}>
                    <div className={"d-inline"} style={{ width: "60%" }}>
                        <span style={{ textAlign: "left", fontSize: 22, fontWeight: "bold" }}>{"Customer Type: "}</span>
                        <span style={{ textAlign: "left", fontSize: 22, textDecoration: "underline" }}>
                            <input
                                type="text"
                                value={customerType}
                                onChange={e => setCustomerType(e.target.value)}
                            />
                            {/* {assessment.customerType} */}
                        </span>
                    </div>

                    <div style={{ width: "40%" }}>
                        <span style={{ textAlign: "left", fontSize: 22, fontWeight: "bold" }}>{"Customer Document: "}</span>
                        <span style={{ textAlign: "left", fontSize: 22, textDecoration: "underline" }}>
                            <input
                                type="text"
                                value={presentedDocument}
                                onChange={e => setPresentedDocument(e.target.value)}
                            />
                            {/* {assessment.presentedDocument} */}
                        </span>
                    </div>

                </div>
                <div style={{ paddingTop: 30 }} >
                    <select
                        value={newRow.equipmentId}
                        onChange={handleEquipmentChange}
                    >
                        <option value="">Select Equipment</option>
                        {equipments.map(equipment => (
                            <option key={equipment.id} value={equipment.id}>{equipment.name}</option>
                        ))}
                    </select>
                    <input
                        type="text"
                        placeholder="Power Rate"
                        value={newRow.powerRate}
                        onChange={e => setNewRow({ ...newRow, powerRate: e.target.value })}
                    />
                     <input
                        type="text"
                        placeholder="quantity"
                        value={newRow.quantity}
                        onChange={e => setNewRow({ ...newRow, quantity: e.target.value })}
                    />
                     <input
                        type="text"
                        placeholder="totalKwh"
                        value={newRow.totalKwh}
                        onChange={e => setNewRow({ ...newRow, totalKwh: e.target.value })}
                    />
                    <button onClick={handleAddRow}>Add Row</button>
                </div>

                <table style={{
                    fontFamily: "arial", borderCollapse: "collapse", width: "100%"
                }}>
                    <tr>
                        <th style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white', width: '30%' }}>Equipment</th>
                        <th style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white', width: '25%' }}>Power Rate</th>
                        <th style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white', width: '20%' }}>Quantity</th>
                        <th style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white', width: '25%' }}>Total Kwh</th>
                    </tr>

                    {customerLoad != null ? customerLoad.map((item, index) => {

                        return (

                            <tr key={item.id}>
                                <td style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20 }}>
                                    {item.equipment}
                                </td>
                                <td style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20 }}>
                                    {/* {item.powerRate} */}
                                    <input
                                        type="text"
                                        value={item.powerRate}
                                        onChange={e => handleChange(e, index, 'powerRate')}
                                    />
                                </td>
                                <td style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20 }}>
                                    {/* {item.quantity} */}
                                    <input
                                        type="text"
                                        value={item.quantity}
                                        onChange={e => handleChange(e, index, 'quantity')}
                                    />
                                </td>
                                <td style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20 }}>
                                    {/* {item.totalKwh} */}
                                    <input
                                        type="text"
                                        value={item.totalKwh}
                                        onChange={e => handleChange(e, index, 'totalKwh')}
                                    />
                                </td>
                            </tr>

                        )
                    }, this) : <div></div>}

                </table>
            </CModalBody>
            <CModalFooter>
                <div className="d-flex justify-content-between">
                    <button type="button" className="btn btn-outline-danger m-2 btn-sm" onClick={onClose}>
                        Close
                    </button>

                    <button
                        disabled={loading}
                        className="btn btn-outline-success m-2 btn-sm"
                        type="submit"
                        onClick={(e) => handleUpdateAssessment(e)}>
                        Edit
                    </button>

                </div>
            </CModalFooter>
        </CModal>
    );
}
export default EditAssessment;