import React from 'react';
import Inspections from "./views/dashboard/Inspections";

const Inspection = React.lazy(() => import('./views/dashboard/Inspections'));
const Users = React.lazy(() => import('./views/users/Users'));
const Roles = React.lazy(() => import('./views/users/Roles'));


const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/inspection', name: 'Inspections', component: Inspection },
  { path: '/users', exact: true,  name: 'Users', component: Users },
  { path: '/roles', exact: true, name: 'User Roles', component: Roles },
];

export default routes;
