import { getRootUrl } from './rootUrl';
import axios from 'axios';




export const loadInspectionsByDateRange = (token, startDate, endDate,statuses,activePage,itemsPerPage,params) => {
  const rootUrl = getRootUrl().cm_url;
  const config = {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };
  const statusString = statuses ? statuses.join(',') : '';
  return axios.get(
    rootUrl + `/inspections/getAdminInspectionSByDate?startDate=${startDate}&endDate=${endDate}&page=${activePage}&limit=${itemsPerPage}&statuses=${statusString}&${params}`,
    config).then(response => response.data)
};
export const exportInspections = (token, startDate, endDate,statuses,params) => {
  const rootUrl = getRootUrl().cm_url;
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
      Accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
      
    },
    responseType: 'arraybuffer',
  };
  const statusString = statuses ? statuses.join(',') : '';
  return axios.get(
    rootUrl + `/inspections/exportAdminToExcel?startDate=${startDate}&endDate=${endDate}&statuses=${statusString}&${params}`,
    config).then(response => {
      const data = new Uint8Array(response.data);
      return data;
    })
};

