import * as React from 'react';
import {useEffect, useState} from 'react';
import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineOppositeContent from '@mui/lab/TimelineOppositeContent';
import TimelineDot from '@mui/lab/TimelineDot';
import Typography from '@mui/material/Typography';
import {useStore} from "../../MobxStore";
import {loadHistory} from "../../services/historyClient";
import {CBadge, CCardBody,CModal,CModalBody,CModalHeader,CModalFooter,CButton} from "@coreui/react";
import logo from "../../assets/logo.png";
import Moment from "moment";
import DialogContentText from "@material-ui/core/DialogContentText";


const getBadge = status => {
  switch (status) {
    case 3:
      return 'danger';
    case 10:
      return 'danger';
    case 11:
      return 'secondary';
    case 12:
      return 'warning';
    case 13:
      return 'success';
    case 14:
      return 'secondary';
    case 15:
      return 'primary';
    case 16:
      return 'info';
    case 20:
      return 'danger';
    case 22:
      return 'success';
    case 21:
      return 'info';
    default:
      return 'primary'
  }
};

const priorityBadge = status => {
  switch (status) {
    case "High":
      return 'danger';
    case "Medium":
      return 'warning';
    case "Low":
      return 'primary';
    default:
      return 'primary'
  }
};





  export const TaskHistory = React.forwardRef((props, ref) => {

  const store = useStore();
  const history = props.task

  const [historyList, setHistory] = useState([]);
  const [remarkModal, setRemarkModal] = useState(false);
  const [selectedIndex, setSelectedIndex] = useState(0);

  useEffect(() => {


    load_history();
  }, []);

  const load_history = () => {

    loadHistory(store.login.token, history.id).then(response => {
      setHistory(response);
      //console.log(response);
    })
      .catch(err => console.log(err));
  };

  return (
    <>
    <CModal
    show={remarkModal}
    close
    onClose={() => setRemarkModal(false)}
    closeOnBackdrop={true}
    >
      <CModalHeader id="alert-dialog-slide-title" closeButton>
      <h1 style={{
            textAlign: "center",
            fontSize: 20,
            fontWeight: "bold"
          }}>Remark</h1>
      </CModalHeader>
      <CModalBody>
        {historyList &&
          historyList[selectedIndex] &&
          historyList[selectedIndex].additionalNote && (
            <div
              dangerouslySetInnerHTML={{
                __html: historyList[selectedIndex].additionalNote.replace(/\n/g, "<br>")
              }}
            />
          )}
      </CModalBody>
      <CModalFooter>
        <CButton>

        </CButton>
      </CModalFooter>
    </CModal>
    <Timeline position="alternate" ref={ref}>
       <CCardBody>
        <div className="d-flex justify-content-between">
          <div style={{width: "50%"}}>
            <div className="d-flex justify-content-start">
              <div style={{ width: "40%"}}><h6><strong>Customer Name</strong></h6></div>
              <div style={{marginRight: 4, width: "60%"}}><h6>
                {history.customerName}
              </h6></div>
            </div>
            <div>
                <div>
                  <div className="d-flex justify-content-start">
                    <div style={{width: "40%"}}>
                      <h6><strong>Problem Type</strong>
                      </h6></div>
                    <div>
                      <h6>{history.problemType}</h6>
                    </div>
                  </div>
                </div>

            </div>
          </div>
          <div style={{width: "50%"}}>
            <div className="d-flex justify-content-start">
              <div style={{width: "40%",}}><h6 ><strong>Meter Number</strong></h6></div>
              <div style={{marginRight: 4, width: "60%",}}><h6>
                {history.metterNumber}
              </h6></div>
            </div>
            <div className="d-flex justify-content-start">
              <div style={{ width: "40%"}}><h6 ><strong>Registered
                On</strong></h6></div>
              <div style={{marginRight: 4,width: "60%"}}><h6>
                {Moment(new Date(parseFloat(history.registeredDate)).toString()).format('DD/MM/yyyy hh:mm:ss A')}
              </h6></div>
            </div>
          </div>
        </div>

        <div className="d-flex justify-content-between">
          <div style={{width: "50%"}}>
            <div className="d-flex justify-content-start">
              <div style={{ width: "40%"}}><h6><strong>Location</strong></h6></div>
              <div style={{marginRight: 4, width: "60%"}}><h6>
                {history.location}
              </h6></div>
            </div>


          </div>

          <div style={{width: "50%"}}>

            <div className="d-flex justify-content-start">
              <div style={{ width: "40%"}}>
                <h6><strong>Priority Level</strong></h6>
              </div>
              <div><CBadge color={priorityBadge(history.priorityLevel)}>
                {history.priorityLevel}
              </CBadge></div>
            </div>
          </div>

        </div>




      </CCardBody>
      <div><h4 ><strong>Inspection History</strong></h4></div>

      <Timeline position="alternate" style={{width:"100%"}}>
      {historyList.map(function(history, index){
        // console.log(history);
        return <TimelineItem>

            <TimelineOppositeContent
              sx={{ m: 'auto 0' }}
              variant="body2"
              color="text.secondary"
            >
              <div>{history.actionByName}</div>
              <div>{Moment(new Date(parseFloat(history.actionDate)).toString()).format('DD/MM/yyyy hh:mm:ss A')}</div>
              {history.additionalNote ? (
                    <CButton
                      onClick={() => {
                        setRemarkModal(true);
                        setSelectedIndex(index);
                      }}
                      className="btn btn-outline-success m-3 btn-sm"
                    >
                      view remark
                    </CButton>
                  ) : (
                    ""
                  )
              }
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineConnector />
              <TimelineDot color="warning">
                {/*<LaptopMacIcon />*/}
              </TimelineDot>
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent sx={{ py: '12px', px: 2 }}>
              <Typography component="span" style={{color:"darkgreen"}}>
                {history.actionType}
              </Typography>
              <h6>{history.actionDetails}</h6>
              {/* {history.additionalNote!=null?<h6 style={{color:"orangered"}}>{history.additionalNote}</h6>:""} */}

            </TimelineContent>
          </TimelineItem>

      })}
      </Timeline>

    </Timeline>
    </>
  );
});

