import React, {useEffect, useState} from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  CCreateElement,
  CSidebar,
  CSidebarBrand,
  CSidebarNav,
  CSidebarNavDivider,
  CSidebarNavTitle,
  CSidebarMinimizer,
  CSidebarNavDropdown,
  CSidebarNavItem, CImg,
} from '@coreui/react'

import CIcon from '@coreui/icons-react'

// sidebar nav config
// import navigation from './_nav'
import {useStore} from '../MobxStore';
import logo from "../assets/jedco.png";
import logoMin from "../assets/jedcoMin.png";

const TheSidebar = () => {
  const dispatch = useDispatch()
  const show = useSelector(state => state.sidebarShow)



  const store = useStore();

  const [components, setComponents]= useState([]);

  useEffect(() => {
    const _nav =  [];


    // if(store.login.roles.includes("VIEW_COMPLAINT_IN_LAB")||store.login.roles.includes("UPDATE_COMPLAINT_IN_LAB")){
     _nav.push(  {
       _tag: 'CSidebarNavItem',
       name: 'Inspections',
       to: '/inspection',
       icon: <CIcon name="cil-spreadsheet" customClasses="c-sidebar-nav-icon"/>,

     });
     if(store.login.roles.includes("VIEW_USER")){
      _nav.push({
        _tag: 'CSidebarNavTitle',
        _children: ['User Management']
      },
      {
        _tag: 'CSidebarNavItem',
        name: 'Users',
        to: '/users',
        icon: 'cil-user',
      }, {
        _tag: 'CSidebarNavItem',
        name: 'User Roles',
        to: '/roles',
        icon: 'cil-pencil',
      });
     }
      
    // }


    setComponents(_nav)
  }, []);

  return (
    <CSidebar
      show={show}
      onShowChange={(val) => dispatch({type: 'set', sidebarShow: val })}
    >
      <CSidebarBrand className="d-md-down-none" to="/">
        <CImg
          src={logo}
          className="c-sidebar-brand-full"
          alt="Jedco Inspections"
        />
        <h4
          style={{marginLeft:10}}
          className="c-sidebar-brand-full">
          Jedco Inspections
        </h4>
        <CImg
          src={logoMin}
          className="c-sidebar-brand-minimized"

        />
      </CSidebarBrand>
      <CSidebarNav>

        <CCreateElement
          items={components}
          components={{
            CSidebarNavDivider,
            CSidebarNavDropdown,
            CSidebarNavItem,
            CSidebarNavTitle
          }}
        />
      </CSidebarNav>
      <CSidebarMinimizer className="c-d-md-down-none"/>
    </CSidebar>
  )
}

export default React.memo(TheSidebar)
