import React from 'react'
import {
  CBadge,
  CDropdown,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CImg
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import userLogo from '../assets/11.png'
import {useStore} from '../MobxStore';
import {NotificationManager} from "react-notifications";



const TheHeaderDropdown = () => {
  const store = useStore();
  const handleOnIdle = event => {

    const logout = {"status": false, "token": "", "authorities": []};

    if (store.login.status) {
      store.setLogin(logout);
      // NotificationManager.warning('You have been logged out because of idle timeout!', 'Idle TimeOut', 12000);
    }

  };


  return (
    <CDropdown
      inNav
      className="c-header-nav-items mx-2"
      direction="down"
    >
      <CDropdownToggle className="c-header-nav-link" caret={false}>
        <h5  className="mx-2">
        {store.login.name}
        </h5>
        <div className="c-avatar">
          <CImg
            src={userLogo}
            className="c-avatar-img"
            alt="admin@bootstrapmaster.com"
          />
        </div>
      </CDropdownToggle>
      <CDropdownMenu className="pt-0" placement="bottom-end">
        <CDropdownItem
          header
          tag="div"
          color="light"
          className="text-center"
        >
          <strong>Account</strong>
        </CDropdownItem>

        {/* <CDropdownItem>
          <CIcon name="cil-user" className="mfe-2" />Profile
        </CDropdownItem>
        <CDropdownItem>
          <CIcon name="cil-settings" className="mfe-2" />
          Settings
        </CDropdownItem> */}
        <CDropdownItem divider />
        <CDropdownItem
          color="light"
          onClick={()=>{
             handleOnIdle();

          }}
        >
          <CIcon name="cil-lock-locked" className="mfe-2" />
         <strong>Log out</strong>
        </CDropdownItem>

      </CDropdownMenu>
    </CDropdown>
  )
}

export default TheHeaderDropdown
