import { CModal, CModalHeader, CModalBody, CModalFooter, CContainer, CRow, CCol, CFormGroup, CLabel, CInput, CForm } from "@coreui/react"
import { useState } from "react"
import { MultiSelect } from 'primereact/multiselect';

import {updateCustomerInfo} from '../../services/inspectionClient';
import { useStore } from "src/MobxStore";
import { NotificationManager } from "react-notifications";

const EditInfoComponent = ({ show, onClose, onEdit, inspection, problemTypes }) => {
    const store = useStore();
    const [customerName, setCustomerName] = useState(inspection.customerName);
    const [phoneNumber, setPhoneNumber] = useState(inspection.phoneNumber);
    const [meterNumber, setMeterNumber] = useState(inspection.metterNumber);
    const [loading,setLoading]= useState(false);
    const [selectedProblemTypes, setSelectedProblemTypes] = useState(inspection.problemType.split(","));
    const handleUpdateCustomerInfo = ()=>{

        setLoading(true);
        const data={
            customerName: customerName,
            meterNumber: meterNumber,
            phoneNumber: phoneNumber,
            problemType: selectedProblemTypes.join(",")
        }
        updateCustomerInfo(store.login.token,inspection.id,data).then(response=>{
            if(response.status){
                NotificationManager.success(response.message, 'Success', 3000);
                setLoading(false);
                onEdit();
            }
            else {
                NotificationManager.error(response.message, 'Failed!', 3000);
                setLoading(false);
              }
        })
        .catch(err => {
            setLoading(false);
            NotificationManager.error("Check your connection", "Network Error", 3000);
          });
    }
    return (
        <CModal
            show={show}
            onClose={onClose}
            size='lg'
            backdrop
            close
            closeOnBackdrop={false}
        >
            <CModalHeader size="modal-lg" closeButton >
                <h5 style={{
                textAlign: "center",
                fontSize: 20,
                fontWeight: "bold"
            }}>Edit Customer Info
            </h5></CModalHeader>
            <CModalBody>
                <CContainer>
                    <CForm>
                        <CRow>
                            <CCol>
                                <CFormGroup>
                                    <CLabel htmlFor="edit-customer-name">Customer Name</CLabel>
                                    <CInput
                                        id="edit-customer-name"
                                        type="text"
                                        name="edit-customer-name"
                                        value={customerName}
                                        onChange={(e) => setCustomerName(e.target.value)}
                                    />
                                </CFormGroup>
                            </CCol>
                            <CCol>
                                <CFormGroup>
                                    <CLabel htmlFor="edit-meter-number">Meter Number</CLabel>
                                    <CInput
                                        id="edit-meter-number"
                                        type="text"
                                        name="edit-meter-number"
                                        value={meterNumber}
                                        onChange={(e) => setMeterNumber(e.target.value)}
                                    />
                                </CFormGroup>
                            </CCol>
                        </CRow>
                        <CRow>
                            <CCol>
                                <CFormGroup>
                                    <CLabel htmlFor="edit-phone">PhoneNumber</CLabel>
                                    <CInput
                                        id="edit-phone"
                                        type="text"
                                        name="edit-phone"
                                        value={phoneNumber}
                                        onChange={(e) => setPhoneNumber(e.target.value)}
                                    />
                                </CFormGroup>
                            </CCol>
                            <CCol className="form-group">
                                <div>
                                    <CLabel htmlFor="edit-problem-type">Problem Types</CLabel>
                                </div>

                                <MultiSelect
                                    id="edit-problem-type"
                                    name="edit-problem-type"
                                    value={selectedProblemTypes}
                                    placeholder="Select Statuses"
                                    onChange={e => setSelectedProblemTypes(e.value)}
                                    options={problemTypes}
                                    className="w-full md:w-20rem"
                                    maxSelectedLabels={3}
                                    filter
                                    display='comma'
                                    appendTo="self"
                                />

                            </CCol>
                        </CRow>
                    </CForm>
                </CContainer>
            </CModalBody>
            <CModalFooter>
                <div className="d-flex justify-content-between">
                    <button type="button" className="btn btn-outline-danger m-2 btn-sm" onClick={onClose}>
                        Close
                    </button>

                    <button
                    disabled={loading}
                    className="btn btn-outline-success m-2 btn-sm"
                    type="submit"
                    onClick={(e) => handleUpdateCustomerInfo(e)}>
                        Edit
                    </button>

                </div>
            </CModalFooter>

        </CModal>
    )
}
export default EditInfoComponent