import { getRootUrl } from './rootUrl';
import axios from 'axios';


export const loadHistory = (token, taskId) => {
  const rootUrl = getRootUrl().cm_url;
  const config = {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };
  return axios.get(
    rootUrl + "/taskHistory/getTaskHistory?typeId="+taskId,
    config).then(response => response.data)
};
