import { useEffect, useState } from "react";
import { useStore } from "src/MobxStore";
import { updateCodeList, getInspectionCodes } from '../../services/inspectionClient';
import { CModal, CModalBody, CModalFooter, CModalHeader } from "@coreui/react";
import { DialogContentText } from "@material-ui/core";
import { NotificationManager } from "react-notifications";
const EditCodeResult = ({ show, onClose, onEdit, inspection }) => {
    const store = useStore();
    const [codeResultList, setCodeResultList] = useState(inspection.codeResultList);
    const [loading, setLoading] = useState(false);
    const [newRow, setNewRow] = useState({ inspectionCode: "", description: "", result: "" });
    const [inspectionCodes, setInspectionCodes] = useState([]);
    const [isMounted, setIsMounted] = useState(true);

    useEffect(() => {
        setIsMounted(true); // Component mounted
        setLoading(true);
        getInspectionCodes(inspection.meterType).then(result => {
            if (isMounted) { // Check if component is still mounted before updating state
                setInspectionCodes(result.data);
                setLoading(false);
            }
        }).catch(err => {
            setLoading(false)
            NotificationManager.error("Check your connection", "Network Error", 3000);
        });
        return () => {
            setIsMounted(false); // Component will unmount, set isMounted to false
        };

    }, [inspection.meterType]);

    const handleUpdateCodelist = () => {
        setLoading(true);
        const request = codeResultList.map(item => ({
            inspectionCodeId: item.inspectionCodeId,
            id: item.id,
            result: item.result
        }));
        updateCodeList(store.login.token, inspection.id, request).then(response => {
            if (response.status) {
                NotificationManager.success(response.message, 'Success', 3000);
                setLoading(false);
                onEdit();
            } else {
                NotificationManager.error(response.message, 'Failed!', 3000);
                setLoading(false);
            }
        }).catch(err => {
            setLoading(false);
            NotificationManager.error("Check your connection", "Network Error", 3000);
        });
    }

    const handleChange = (e, index, key) => {
        const { value } = e.target;
        const newList = [...codeResultList];
        newList[index][key] = value;
        setCodeResultList(newList);
    }
    const handleAddRow = () => {
        setCodeResultList([...codeResultList, newRow]);
        setNewRow({ inspectionCode: "", description: "", result: "" });
    }
    const handleInspectionCodeChange = (e) => {
        const selectedCodeId = e.target.value;
        const selectedCode = inspectionCodes.find(code => code.id == selectedCodeId);
         setNewRow({
            ...newRow,
            inspectionCodeId: selectedCodeId,
            inspectionCode: selectedCode ? selectedCode.code : "",
            description: selectedCode ? selectedCode.description : ""
        });
    };
    return (
        <CModal
            show={show}
            onClose={onClose}
            size='lg'
            backdrop
            close
            closeOnBackdrop={false}
        >
            <CModalHeader size="modal-lg" closeButton >
                <h5 style={{
                    textAlign: "center",
                    fontSize: 20,
                    fontWeight: "bold"
                }}>Edit CodeList
                </h5>
            </CModalHeader>
            <CModalBody>
                <DialogContentText id="alert-dialog-slide-description">

                    {<p style={{
                        textAlign: "center",
                        fontSize: 32,
                        fontWeight: "bold",
                        textDecoration: "underline"
                    }}>{"Meter Code Checklist"}</p>}

                </DialogContentText>

                <div style={{ paddingTop: 5 }} />
                <div className="d-flex justify-content-between" style={{ paddingTop: 3 }}>
                    <div className={"d-inline"} style={{ width: "60%" }}>
                        <span style={{ textAlign: "left", fontSize: 22, fontWeight: "bold" }}>{"Customer Name: "}</span>
                        <span
                            style={{ textAlign: "left", fontSize: 22, textDecoration: "underline" }}>{inspection.customerName}</span>
                    </div>

                    <div className={"d-inline"} style={{ width: "40%" }}>
                        <span style={{ textAlign: "left", fontSize: 22, fontWeight: "bold" }}>{"Meter No: "}</span>
                        <span style={{ textAlign: "left", fontSize: 22, textDecoration: "underline" }}>{inspection.metterNumber}</span>
                    </div>

                </div>

                <div className="d-flex justify-content-between" style={{ paddingTop: 3 }}>
                    <div className={"d-inline"} style={{ width: "60%" }}>
                        <span style={{ textAlign: "left", fontSize: 22, fontWeight: "bold" }}>{"Location: "}</span>
                        <span style={{ textAlign: "left", fontSize: 22, textDecoration: "underline" }}>{inspection.location}</span>
                    </div>

                    <div style={{ width: "40%" }}>
                        <span style={{ textAlign: "left", fontSize: 22, fontWeight: "bold" }}>{"Meter Type: "}</span>
                        <span style={{ textAlign: "left", fontSize: 22, textDecoration: "underline" }}>{inspection.meterType}</span>
                    </div>

                </div>
                <div style={{ paddingTop: 30 }} />


                {/* New Row Input */}
                <div style={{ paddingTop: 30 }}>
                    <span style={{ fontSize: 20, width: "200px", display: "inline-block", overflowWrap: "break-word" }}>{newRow.description}</span>
                    <select
                        value={newRow.inspectionCodeId}
                        onChange={handleInspectionCodeChange}
                    >
                        <option value="">Select Inspection Code</option>
                        {inspectionCodes.map(code => (
                            <option key={code.id} value={code.id}>{code.code}</option>
                        ))}
                    </select>
                    <input
                        type="text"
                        placeholder="Result"
                        value={newRow.result}
                        onChange={e => setNewRow({ ...newRow, result: e.target.value })}
                    />
                    <button onClick={handleAddRow}>Add Row</button>
                </div>


                <table style={{
                    fontFamily: "arial", borderCollapse: "collapse", width: "100%"
                }}>
                    <tr>
                        <th style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white', width: '15%' }}>Code</th>
                        <th style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white', width: '50%' }}>Description</th>
                        <th style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white', width: '30%' }}>Result</th>
                    </tr>

                    {codeResultList != null ? codeResultList.map((item, index) => {

                        return (

                            <tr key={index}>
                                <td style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white' }}>{item.inspectionCode}</td>
                                <td style={{ border: '1px solid #dddddd', textAlign: 'left', padding: '8px', fontSize: 20 }}>{item.description}</td>
                                <td style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20 }}>
                                    <input
                                        type="text"
                                        value={item.result}
                                        onChange={e => handleChange(e, index, 'result')}
                                    />
                                </td>
                            </tr>

                        )
                    }, this) : <div></div>}

                </table>
            </CModalBody>
            <CModalFooter>
                <div className="d-flex justify-content-between">
                    <button type="button" className="btn btn-outline-danger m-2 btn-sm" onClick={onClose}>
                        Close
                    </button>

                    <button
                        disabled={loading}
                        className="btn btn-outline-success m-2 btn-sm"
                        type="submit"
                        onClick={(e) => handleUpdateCodelist(e)}>
                        Edit
                    </button>

                </div>
            </CModalFooter>
        </CModal>
    );

}
export default EditCodeResult;