import { useState } from "react";
import { useStore } from "src/MobxStore";
import { updateCheckList } from '../../services/inspectionClient';
import { CModal, CModalBody, CModalFooter, CModalHeader } from "@coreui/react";
import { DialogContentText } from "@material-ui/core";
import { NotificationManager } from "react-notifications";

const EditCheckList = ({ show, onClose, onEdit, inspection }) => {
    const store = useStore();
    const [checkListResultList, setCheckListResultList] = useState(inspection.checkListResultList);
    const [loading, setLoading] = useState(false);

    const handleUpdateChecklist = () => {
        setLoading(true);
        const request = checkListResultList.map(item => ({
            id: item.id,
            status: item.checklistStatus,
            remark: item.remark
        }));
        updateCheckList(store.login.token, request).then(response => {
            if (response.status) {
                NotificationManager.success(response.message, 'Success', 3000);
                setLoading(false);
                onEdit();
            } else {
                NotificationManager.error(response.message, 'Failed!', 3000);
                setLoading(false);
            }
        }).catch(err => {
            setLoading(false);
            NotificationManager.error("Check your connection", "Network Error", 3000);
        });
    }

    const handleChange = (e, index, key) => {
        const { value } = e.target;
        const newList = [...checkListResultList];
        newList[index][key] = value;
        console.log(newList);
        setCheckListResultList(newList);
    }

    return (
        <CModal
            show={show}
            onClose={onClose}
            size='lg'
            backdrop
            close
            closeOnBackdrop={false}
        >
            <CModalHeader size="modal-lg" closeButton >
                <h5 style={{
                    textAlign: "center",
                    fontSize: 20,
                    fontWeight: "bold"
                }}>Edit CheckList
                </h5></CModalHeader>
            <CModalBody>
                <DialogContentText id="alert-dialog-slide-description">

                    {<p style={{
                        textAlign: "center",
                        fontSize: 32,
                        fontWeight: "bold",
                        textDecoration: "underline"
                    }}>{"Meter Installation Checklist"}</p>}

                </DialogContentText>

                <table style={{
                    fontFamily: "arial", borderCollapse: "collapse", width: "100%"
                }}>
                    <tr>
                        <th style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white' }}>S/N</th>
                        <th style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white', width: '40%' }}>CheckList</th>
                        <th style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white' }}>Status</th>
                        <th style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white', width: '40%' }}>Remark</th>
                    </tr>

                    {checkListResultList && inspection.checkListResultList.sort((a, b) => a.checkListId > b.checkListId ? 1 : -1).map((item, index) => {
                        return (

                            <tr key={item.id}>
                                <td style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20, backgroundColor: 'grey', color: 'white' }}>{item.checkListId}</td>
                                <td style={{ border: '1px solid #dddddd', textAlign: 'left', padding: '8px', fontSize: 20 }}>{item.checkList}</td>
                                <td style={{ border: '1px solid #dddddd', textAlign: 'center', padding: '8px', fontSize: 20 }}>
                                    <select
                                    value={item.checklistStatus}
                                    onChange={e => handleChange(e, index, 'checklistStatus')}
                                >
                                    <option value={true}>Yes</option>
                                    <option value={false}>No</option>
                                </select></td>
                                <td style={{ border: '1px solid #dddddd', textAlign: 'left', padding: '8px', fontSize: 20 }}>
                                    <textarea
                                        rows={3}
                                        value={item.remark}
                                        onChange={e => handleChange(e, index, 'remark')}
                                    />
                                </td>
                            </tr>

                        )
                    }, this)}

                </table>
            </CModalBody>
            <CModalFooter>
                <div className="d-flex justify-content-between">
                    <button type="button" className="btn btn-outline-danger m-2 btn-sm" onClick={onClose}>
                        Close
                    </button>

                    <button
                        disabled={loading}
                        className="btn btn-outline-success m-2 btn-sm"
                        type="submit"
                        onClick={(e) => handleUpdateChecklist(e)}>
                        Edit
                    </button>

                </div>
            </CModalFooter>
        </CModal>
    )

}
export default EditCheckList;