import { getRootUrl } from './rootUrl';
import axios from 'axios';

export function authLogin(username, password) {
  const rootUrl = getRootUrl().um_url;
  return axios({
    method: 'post',
    url: rootUrl + "/auth/login",
    data: {
      "username": username,
      "password": password
    }
  }).then(response => response.data)
}

export function listStatuses(token) {
  const rootUrl = getRootUrl().um_url;
  const config = {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };
  return axios.get(
    rootUrl + "/statuses/getAllStatuses",
    config).then(response => response.data)
}

export function listAssessmentbyId(token, id) {
  const rootUrl = getRootUrl().um_url;
  const config = {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };
  return axios.get(
    rootUrl + "/assessment/listAssessmentsByInspection?inspectionId="+id,
    config).then(response => response.data)
}
export function getAllProblemTypes(){
  const rootUrl = getRootUrl().um_url;
  return axios.get(
    rootUrl + "/problemTypes/getAllProblemTypes"
  ).then(response => response.data)
}
